@import url(crt.css);
@font-face {
  font-family: VT323;
  src: url(../public/VT323-Regular.ttf);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "VT323", "Courier New", Courier, monospace;
}

body {
  background-color: black;
  position: relative;
  color: white;
  font-size: 20px;
}

#content {
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding-top: 20px;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px 30px;
  max-width: 1660px;
}

footer {
  display: flex;
  align-items: center;
  padding: 30px;
  color: white;
}

table {
  width: fit-content;
  border-collapse: collapse;
}

td {
  padding: 0 10px 0 0;
}

a {
  text-decoration: none;
  color: rgb(0, 220, 180);
}

a:hover {
  color: rgb(0, 255, 200);
}

a:active {
  color: rgb(0, 175, 150);
  font-weight: bold;
}

a.active {
  color: rgb(0, 255, 200);
  font-weight: bold;
}

pre {
  white-space: pre-wrap;
}

.header {
  font-size: 1.3em;
  font-weight: normal;
}
.cyan {
  color: rgb(0, 255, 200);
  font-style: normal;
  font-weight: 100;
}

::selection {
  color: black;
  background-color: rgb(87, 137, 137);
}

.buzz_wrapper {
  position: relative;
  min-height: 100vh;
  background-color: #000;
  overflow-y: auto;
  padding: 0;
}

.list {
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin-top: 20px;
}

.list-item {
  display: flex;
  flex-direction: column;
}

.space-top {
  margin-top: 5px;
}

.glitch {
  position: relative;
  width: 100%;
}

.glitch-layer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  pointer-events: none;
  font-size: inherit;
  animation: jerkwhole 5s infinite;
  white-space: pre-wrap;
}

.glitch-layer:nth-child(1) {
  transform: translate(-1px, 0.5px);
  z-index: 1;
  animation: flickersmall 30ms infinite, jerk 50ms infinite;
}
.glitch-layer:nth-child(1) * {
  color: red !important;
  border-color: red !important;
}
.glitch-layer:nth-child(2) {
  transform: translate(1px, -0.5px);
  animation: flickersmall 30ms infinite, jerk 50ms infinite, jerkgreen 1s infinite;
  z-index: 2;
}
.glitch-layer:nth-child(2) * {
  color: green !important;
  border-color: green !important;
}
.glitch-layer:nth-child(3) {
  animation: flickersmall 30ms infinite, jerk 50ms infinite, jerkblue 1s infinite;
  z-index: 3;
}
.glitch-layer:nth-child(3) * {
  color: blue !important;
  border-color: blue !important;
}
.glitch-layer:nth-child(4) {
  color: white;
  z-index: 4;
}

#content {
  filter: blur(0.5px);
}

.buzz_wrapper.scrolling .glitch-layer,
.crt.scrolling::after,
.crt.scrolling::before {
  animation-play-state: paused !important;
}

@media (min-width: 1100px) {
  #content {
    font-size: 1.05em;
  }
}
